#main-wrapper {
    width: 100vw;
    min-width: 100%;
}

#wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

body {
    height: 100vh;
    overflow-x: hidden;
    max-width: 100vw;
    width: 100vw;
    position: relative;
    min-height: 100vh;
    font-family: $SFProDisplay;
    font-weight: 300;
    color: #0e0e0f;

    &.error404 {
        overflow: hidden;
    }

    &.popup-opened {
        overflow: hidden;

        @media screen and (max-width: 768px) {
            height: 100vh;
        }
    }
}

.container {
    padding: 0 16px;
    margin: 0 auto;
    width: 1587px;
    max-width: 100%;

    @include max-break('1470px') {
        width: 1370px;
    }
    @include max-break('1400px') {
        width: 1250px;
    }
    @include max-break('1280px') {
        width: 1050px;
    }
    @include max-break('1070px') {
        width: 100%;
    }

    &.full-width {
        padding: 0;
        width: 100vw;
        max-width: 100vw;
    }
}

.circle-link {
    width: 46px;
    height: 46px;
    background-color: #e41019;
    border: 1px solid #e41019;
    border-radius: 50%;
    transition: background-color 0.3s;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url(data:image/svg+xml;base64,);


    @include max-break('768px') {
        width: 40px;
        height: 40px;
        background-size: 18px 10px;
    }

    &:hover {
        background-color: #fff;
        background-image: url(data:image/svg+xml;base64,);
    }
}

.arrow-link {
    cursor: pointer;
    border: none;
    background-color: transparent;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #e41019;
    font-family: $SFProDisplay;
    font-size: 22px;
    font-style: normal;
    font-weight: 300;
    line-height: 1.1;
    position: relative;
    padding: 11px 61px 11px 0;

    @include max-break('768px') {
        font-size: 18px;
        padding: 11px 50px 11px 0;
    }

    &:after {
        content: '';
        display: block;
        position: absolute;
        width: 46px;
        height: 46px;
        top: 50%;
        right: 0;
        transition: background-color 0.3s;
        transform: translateY(-50%);
        border: 1px solid #e41019;
        border-radius: 50%;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 21px 11px;
        background-image: url(data:image/svg+xml;base64,);

        @include max-break('768px') {
            width: 40px;
            height: 40px;
            background-size: 18px 10px;
        }
    }

    &.arrow-link-reverse {
        flex-direction: row;
        padding: 11px 0 11px 61px;

        @include max-break('768px') {
            padding: 11px 0 11px 50px;
        }

        &:after {
            transform: translateY(-50%) scale(-1, -1);
            right: auto;
            left: 0;
        }
    }

    &:hover {
        &:after {
            background-color: #e41019;
            background-image: url(data:image/svg+xml;base64,);
        }
    }
}
